import { START_LOADING, 
		STOP_LOADING,
	} from '../actions/loading';


const initialState =  {
  loading:false
};

export default function loading(state = initialState, action) {

  switch (action.type) {

    case START_LOADING:
      console.log(__dirname)
      return {
        ...state, loading: true
      };
    case STOP_LOADING:
      return {
        ...state, loading: false
      };

    default:
      return state;
  }
}


